import {
  Input,
  message,
  Form,
  Row,
  Col,
  Select,
  Button,
  Modal,
  Table,
} from "antd"
import { useEffect, useState } from "react"
import axios from "axios"
import "antd/dist/antd.css"
import "./App.css"
import getTranslation from "./util/translate"
const { TextArea } = Input

console.log("process", process)
console.log("process env", process.env.REACT_APP_DEV)
// 根据环境变量处理请求地址
export const BASEURL = process.env.REACT_APP_DEV
  ? "http://192.168.3.69:18000"
  : ""
// const BASEURL = process.env.REACT_APP_PROD ? "" : "http://192.168.3.187:18000"  // 后端本地ip

const { Item } = Form
const { Option } = Select
const formlayout = {
  labelCol: {
    span: 8,
  },
  wrapperCol: {
    span: 15,
  },
}
const isChinese = navigator.language.includes("zh")
export default function DataSet() {
  document.title = isChinese ? "取数设置" : "DBSetting"
  const [db, setDB] = useState({})
  const [source, setSource] = useState({
    type: undefined,
    host: "",
    port: "",
    username: "",
    password: "",
    database: "",
    table: "",
    multiTable: "0", //多表查询value
  })
  const [fieldSource, setFiledSource] = useState([])
  const [fields, setFileds] = useState([])
  const [tables, setTables] = useState([])
  const [cycle, setCycle] = useState(undefined)
  const [where, setWhere] = useState("")
  const [store_number, setStore_number] = useState("")
  const [folder, setFolder] = useState({})
  const [folders, setFolders] = useState([])

  const [showModal, setShowModal] = useState(false) // 结果预览Modal弹窗状态
  const [tablData, setTablData] = useState([]) //结果预览table数据
  const [loading, setLoading] = useState(false) //结果预览table数据loading

  // 结果预览接口请求
  const resultData = async () => {
    // 先清空数据
    setTablData([])
    try {
      setShowModal(true)
      const body = {
        priceUnit: db.priceUnit,
        unit: db.unit,
        status: 1,
        source,
        fieldSource,
        cycle,
        where,
        store_number,
      }
      axios.post(`${BASEURL}/api/db/topnum`, body).then((res) => {
        if (res.status != 200) {
          message.error("结果预览接口请求失败")
          setLoading(false)
          return
        }
        const { jsonstr } = res.data
        if (jsonstr) {
          const tableList = JSON.parse(jsonstr) || []
          setTablData(tableList)
          setLoading(false)
        }
      })
    } catch (error) {
      console.log(error)
      setLoading(false)
      message.error("结果预览接口请求失败")
    }
  }

  // 获取文件夹
  const queryFolders = () => {
    console.log(BASEURL)
    let params = { path: folder.name || "" }
    axios
      .post(`${BASEURL}/api/db/file/search`, JSON.stringify(params))
      .then((res) => {
        const { code, data } = res.data
        let dirs = []
        if (code === 200) {
          if (data && data.files) {
            ;(data.files || []).forEach((item) => {
              if (/\.db$/.test(item)) dirs.push({ name: item, isFile: true })
            })
          }
          if (data && data.dirs)
            dirs = dirs.concat(
              data.dirs.map((item) => ({ name: item + "\\", isFile: false }))
            )
          setFolders(dirs)
        } else {
          message.error(res.data.msg || "")
        }
      })
      .catch((error) => {
        message.error(`${error}`)
      })
  }

  useEffect(() => {
    if (folder && !folder.isFile) queryFolders()
  }, [folder])

  const onSelectFolder = (index) => {
    if (index >= 0) {
      setFolder((folder) => {
        let name, isFile
        if (folder && folder.isFile) {
          name = folder.name
          isFile = true
        } else {
          isFile = folders[index].isFile
          if (folder && folder.name) name = folder.name + folders[index].name
          else name = folders[index].name
        }
        onChange("host", name)
        return { name, isFile }
      })
    } else {
      onChange("host", "")
      setFolder({})
    }
  }

  const onChange = (e, value) => {
    const s = { ...source }
    switch (e) {
      case "frequency":
        setCycle(value)
        return
      case "priceUnit":
        setDB({ ...db, priceUnit: value })
        return
      case "where":
        setWhere(value)
        return
      case "store_number":
        setStore_number(value)
        return
      case "type":
        s.type = value
        break
      case "host":
        s.host = value
        break
      case "port":
        s.port = value
        break
      case "username":
        s.username = value
        break
      case "password":
        s.password = value
        break
      case "database":
        s.database = value
        break
      // case "multiTable":
      //   s.multiTable = value
      //   //
      //   //清除数据设置内多表查询区分的值
      //   const newValue = [...fieldSource]
      //   fieldSource.map((v) => {
      //     v.value = ""
      //   })
      //

      default:
        break
    }
    setSource({ ...s })
  }

  const onSetChange = (index, value) => {
    const s = [...fieldSource]
    s[index].value = value
    setFiledSource([...s])
  }

  const onUnitChange = (index, value) => {
    const d = { ...db }
    d.unit[index].value = value
    setDB({ ...d })
  }
  const onTable = (e, notRequest) => {
    setSource({ ...source, table: e })
    if (!notRequest) {
      getFields(e)
    }
  }

  // 获取
  const getFields = (table) => {
    axios
      .post(`${BASEURL}/api/db/fields`, { source: { ...source, table } })
      .then((res) => {
        const { code, data } = res.data
        if (code === 200) {
          let f = []
          if (data && data.fields) f = data.fields || []
          setFileds([...f])
          if (res.data.message) message.error(res.data.message)
        } else {
          message.error(res.data.message || "")
        }
      })
      .catch((error) => {
        message.error(`${error}`)
      })
  }

  // 数据库检测
  const checkDb = () => {
    axios
      .post(`${BASEURL}/api/db/check`, { source })
      .then((res) => {
        const { code, data } = res.data
        if (code === 200 && !res.data.message) {
          setTables([...(data.tables || [])])
          message.success(isChinese ? "连接成功" : "Connection successful")
        } else {
          message.error(res.data.message || "")
        }
      })
      .catch((error) => {
        message.error(`${error}`)
      })
  }

  // 保存设置
  const onSave = () => {
    const body = {
      priceUnit: db.priceUnit,
      unit: db.unit,
      status: 1,
      source,
      fieldSource,
      cycle,
      where,
      store_number,
    }
    axios
      .post(`${BASEURL}/api/db/setting`, body)
      .then((res) => {
        const { code } = res.data
        if (code === 200 && !res.data.message) {
          message.success(isChinese ? "保存成功" : "Saved Ok")
        } else {
          message.error(res.data.message || "")
        }
      })
      .catch((error) => {
        message.error(`${error}`)
      })
  }

  useEffect(() => {
    axios
      .get(`${BASEURL}/api/db/setting`)
      .then((res) => {
        const { code, data = {} } = res.data
        if (code === 200) {
          const d = { ...data }
          d.types = Object.entries(data.types || {})
          d.cycleSource = Object.entries(data.cycleSource || {})

          d.priceUnitSource = Object.entries(data.priceUnitSource || {})

          // if (typeof (d.cycle) === "number")
          // d.cycle = d.cycle.toString();

          setDB({ ...d })
          setCycle(d.cycle)
          setWhere(d.where)
          setStore_number(d.store_number)
          const s = data.source || source
          if (typeof s.type === "number") s.type = s.type.toString()
          setSource({ ...s })

          let f = data.fieldSource || []
          setFiledSource([...f])

          setTables(data.tables || [])

          setFileds(data.fields || [])

          updateInitValue(d.cycle.toString())
        } else {
          message.error(res.data.message || "")
        }
      })
      .catch((error) => {
        message.error(`${error}`)
      })
  }, [])

  const updateInitValue = (cycle) => {
    setTimeout(() => {
      setCycle(cycle)
    }, 100)
  }

  // 结果预览表格title
  const columns = [
    {
      title: "plu编号",
      dataIndex: "plu_code",
      key: "plu_code",
      align: "center",
    },
    {
      title: "plu名称",
      dataIndex: "plu_name",
      key: "plu_name",
      align: "center",
    },
    {
      title: "单价",
      dataIndex: "price",
      key: "price",
      align: "center",
    },
    {
      title: "称重单位",
      dataIndex: "price_unit",
      key: "price_unit",
      align: "center",
    },
    {
      title: "保质期天数",
      dataIndex: "save_days",
      key: "save_days",
      align: "center",
    },
    {
      title: "店内码",
      dataIndex: "item_no",
      key: "item_no",
      align: "center",
    },
    {
      title: "附加号",
      dataIndex: "et_no",
      key: "et_no",
      align: "center",
    },
    {
      title: "拼音(助记码)",
      dataIndex: "pin_yin",
      key: "pin_yin",
      align: "center",
    },
  ]

  return (
    <>
      <div className="App">
        <h1 style={{ textAlign: "center" }}>
          {isChinese ? "表单填写" : "Form Submission"}
        </h1>
        <Row>
          <Col span={8} className="col-scroll">
            <Form {...formlayout}>
              <Item
                label={<h2>{isChinese ? "读取设置" : "Read Setting"}</h2>}
                colon={false}
              />
              <Item label={isChinese ? "数据库类型" : "DB Type"}>
                <Select
                  value={source.type}
                  onChange={(e) => onChange("type", e)}
                >
                  {(db.types || []).map((e) => (
                    <Option key={e[0]}>{e[1]}</Option>
                  ))}
                </Select>
              </Item>
              {/* {source.type == 3 && (
                <Item label={isChinese ? "数据库地址" : "Folder Select"}>
                  <Select
                    allowClear
                    onChange={onSelectFolder}
                    value={folder.name}
                  >
                    {folders.map((item, index) => (
                      <Option>{item.name}</Option>
                    ))}
                  </Select>
                </Item>
              )} */}
              {(source.type != 3 || source.type == 3) && (
                <Item label={isChinese ? "数据库地址" : "DB Address"}>
                  <Input
                    value={source.host}
                    onChange={(e) => onChange("host", e.target.value)}
                  />
                </Item>
              )}
              {source.type != 3 && (
                <Item label={isChinese ? "账号" : "Account"}>
                  <Input
                    value={source.username}
                    onChange={(e) => onChange("username", e.target.value)}
                  />
                </Item>
              )}
              <Item label={isChinese ? "密码" : "Password"}>
                <Input.Password
                  visibilityToggle={false}
                  value={source.password}
                  onChange={(e) => onChange("password", e.target.value)}
                />
              </Item>
              {source.type != 3 && (
                <Item label={isChinese ? "端口" : "Port"}>
                  <Input
                    value={source.port}
                    onChange={(e) => onChange("port", e.target.value)}
                  />
                </Item>
              )}
              {source.type != 3 && (
                <Item label={isChinese ? "数据库名称" : "DB Name"}>
                  <Input
                    value={source.database}
                    onChange={(e) => onChange("database", e.target.value)}
                  />
                </Item>
              )}
              <Item label=" " colon={false}>
                <Button
                  type="primary"
                  style={{ background: "#5dba9d", border: "0", width: "50%" }}
                  onClick={checkDb}
                >
                  {isChinese ? "测试" : "Test"}
                </Button>
              </Item>
            </Form>
          </Col>
          <Col span={8} className="col-scroll">
            <Form {...formlayout}>
              <Item
                label={<h2>{isChinese ? "数据设置" : "Data Setting"}</h2>}
                colon={false}
              />
              {/* <Item label={isChinese ? "多表查询" : "multiTable"}>
                <Row gutter={[10]}>
                  <Col span={24}>
                    <Select
                      value={source.multiTable}
                      onChange={(e) => onChange("multiTable", e)}
                    >
                      <Option key={0}>单表</Option>
                      <Option key={1}>多表</Option>
                    </Select>
                  </Col>
                </Row>
              </Item> */}
              <Item label={isChinese ? "表名称" : "Table Name"}>
                <Row gutter={[10]}>
                  {source.multiTable == 1 ? null : (
                    <Col span={12}>
                      <Select
                        value={source.table}
                        onChange={(e) => onTable(e, false)}
                      >
                        {(tables || []).map((e) => (
                          <Option key={e}>{e}</Option>
                        ))}
                      </Select>
                    </Col>
                  )}

                  <Col span={source.multiTable == 1 ? 24 : 12}>
                    <Input.Search
                      enterButton={isChinese ? "确认" : "confirm"}
                      onChange={(e) => onTable(e.target.value, true)}
                      value={source.table}
                      onSearch={(e) => onTable(e, false)}
                    />
                  </Col>
                </Row>
              </Item>
              {/* <Item label=" " colon={false}>
                <Input.Search defaultValue={source.table} onSearch={onTable} />
              </Item> */}
              {fieldSource.map((item, index) => (
                <Item label={getTranslation(item.info)}>
                  <Row gutter={[10]}>
                    {source.multiTable == 1 ? null : (
                      <Col span={12}>
                        <Select
                          value={item.value}
                          onChange={(e) => onSetChange(index, e)}
                        >
                          {(fields || []).map((e) => (
                            <Option key={e}>{e}</Option>
                          ))}
                        </Select>
                      </Col>
                    )}

                    <Col span={source.multiTable == 1 ? 24 : 12}>
                      <Input
                        value={item.value}
                        onChange={(e) => onSetChange(index, e.target.value)}
                      />
                    </Col>
                  </Row>
                </Item>
              ))}
              <Item label={isChinese ? "取数频率" : "Read Frequency"}>
                <Select
                  value={cycle}
                  onChange={(e) => onChange("frequency", e)}
                >
                  {(db.cycleSource || []).map((e) => (
                    <Option key={e[0]}>{getTranslation(e[1])}</Option>
                  ))}
                </Select>
              </Item>
            </Form>
          </Col>
          <Col span={8} className="col-scroll">
            <Form {...formlayout}>
              <Item
                label={<h2>{isChinese ? "单位设置" : "Unit Setting"}</h2>}
                colon={false}
              />
              <Item label={isChinese ? "单价单位" : "Price Unit"}>
                <Select
                  value={db.priceUnit?.toString()}
                  onChange={(e) => onChange("priceUnit", e)}
                >
                  {(db.priceUnitSource || []).map((e) => (
                    <Option key={e[0]}>{getTranslation(e[1])}</Option>
                  ))}
                </Select>
              </Item>
              {(db.unit || []).map((item, index) => (
                <Item label={getTranslation(item.info)}>
                  <Input
                    value={item.value}
                    onChange={(e) => onUnitChange(index, e.target.value)}
                  />
                </Item>
              ))}
              <Item
                label={<h2>{isChinese ? "自定义设置" : "Custom setting"}</h2>}
                colon={false}
              />
              <Item label={isChinese ? "Where设置" : "Where setting"}>
                <TextArea
                  value={where}
                  allowClear
                  onChange={(e) => onChange("where", e.target.value)}
                  rows={6.5}
                />
              </Item>
              <Item label={isChinese ? "门店号" : "Store Number"}>
                <Input
                  allowClear
                  value={store_number}
                  onChange={(e) => onChange("store_number", e.target.value)}
                />
              </Item>
            </Form>
          </Col>
          <Col span={12}></Col>
          <Col span={8}>
            <div style={{ display: "flex", justifyContent: "space-around" }}>
              <Button
                type="primary"
                style={{
                  background: "#5dba9d",
                  border: "0",
                  width: "30%",
                  marginTop: "20px",
                }}
                onClick={onSave}
              >
                {isChinese ? "保存" : "save"}
              </Button>
              <Button
                type="primary"
                style={{
                  background: "#5dba9d",
                  border: "0",
                  width: "30%",
                  marginTop: "20px",
                }}
                onClick={resultData}
              >
                {isChinese ? "结果预览" : "save"}
              </Button>
            </div>
          </Col>
        </Row>

        {/* 结果预览Modal */}
        <Modal
          title="结果预览（最多前5条）"
          width={"60%"}
          visible={showModal}
          keyboard={true}
          closable={false}
          onCancel={() => {
            setShowModal(false)
          }}
          footer={[
            <Button
              onClick={() => {
                setShowModal(false)
              }}
            >
              返回
            </Button>,
          ]}
        >
          <Table
            dataSource={tablData}
            columns={columns}
            rowKey="plu_code"
            pagination={false}
            loading={loading}
          />
        </Modal>
      </div>
    </>
  )
}
