import { Input, Card, Empty, Spin, message } from "antd"
import "antd/dist/antd.css"
import { useEffect, useState } from "react"
import SkuCard from "./components/Card"
import axios from "axios"
import "./App.css"
import { BASEURL } from "./DataSet.js"

const { Search } = Input
const isChinese = navigator.language.includes("zh")
export default function App() {
  document.title = isChinese ? "图片编辑" : "IconEdit"
  const handleSearch = (e) => {
    if (e === name) return
    setSkus([])
    setName(e)
    setCurrent(1)
    setMore(true)
  }

  const onscroll = (event) => {
    if (!hasmore) return
    const {
      target: { clientHeight, scrollHeight, scrollTop },
    } = event
    if (scrollTop + clientHeight >= scrollHeight) {
      setCurrent((c) => c + 1)
    }
  }

  const [current, setCurrent] = useState(1)
  const [name, setName] = useState("")
  const [loading, setLoading] = useState(false)
  const [skus, setSkus] = useState([])
  const [hasmore, setMore] = useState(true)

  useEffect(() => {
    setLoading(true)
    axios
      .get(
        `${BASEURL}/api/sku/search?page_size=200&search=${name}&current_page=${current}`,
        {
          headers: {
            "Access-Control-Allow-Origin": "*",
          },
        }
      )
      .then((res) => {
        const s = res.data.data || []
        if (s.length < 200) setMore(false)
        setSkus((skus) => skus.concat(res.data.data || []))
        setLoading(false)
      })
      .catch((error) => {
        message.error(`${error}`)
        setLoading(false)
      })
  }, [current, name])

  return (
    <>
      <div className="App">
        <Card style={{ height: "80px" }}>
          <Search
            placeholder={
              isChinese
                ? "请输入商品码或者商品名称"
                : "Please input the code or name"
            }
            allowClear
            className="search"
            onSearch={handleSearch}
          />
          <div
            style={{
              color: "red",
              display: "inline-block",
              lineHeight: "30px",
              marginLeft: "40px",
            }}
          >
            温馨提示：图片格式支持JPG和PNG
          </div>
        </Card>
        <Spin size="large" spinning={loading}>
          <div className="skus" onScroll={onscroll}>
            {skus.map((item) => (
              <SkuCard {...item} className="card" />
            ))}
            {!hasmore && (
              <p
                style={{
                  float: "left",
                  width: "100%",
                  textAlign: "center",
                  marginTop: "30px",
                }}
              >
                {isChinese ? "已加载全部" : "All loaded"}
              </p>
            )}
            {skus.length === 0 && (
              <Empty
                description={isChinese ? "当前无商品" : "Empty"}
                style={{ marginTop: "10%" }}
              />
            )}
          </div>
        </Spin>
      </div>
    </>
  )
}
