import { useState, useEffect } from "react"
import { Button, Image, Tooltip, Upload, message } from "antd"
import "antd/dist/antd.css"
import "./index.css"
import { LockOutlined, UnlockOutlined } from "@ant-design/icons"
import default_icon from "../assets/default.png"
import axios from "axios"
import ImgCrop from "antd-img-crop"
import { BASEURL } from "../DataSet.js"

const isChinese = navigator.language.includes("zh")
export default function Card(props) {
  let { sku_code, sku_name, filename, is_lock } = props
  const [isUploading, setLoaidng] = useState(false)
  const [isLocking, setLocking] = useState(false)
  const [img, setImg] = useState(filename || default_icon)
  const [name, setName] = useState("")
  const [lock, setLock] = useState(is_lock)

  const check = (headers) => {
    return (buffers, options = { offset: 0 }) =>
      headers.every(
        (header, index) => header === buffers[options.offset + index]
      )
  }

  const checkImageFile = (file) => {
    // 返回异步对象
    return new Promise((resolve, reject) => {
      // 创建文件读取对象
      const reader = new FileReader()
      // 监听文件读取完成
      reader.onload = () => {
        const bytes = new Uint8Array(reader.result)
        //16进制打印
        // console.log('bytes', HEXab2hex(bytes));
        // 判断是否是png类型
        const isPNG = check([0x89, 0x50, 0x4e, 0x47]) // PNG图片对应的魔数
        // 判断是否是jpg类型
        const isJPG = check([0xff, 0xd8, 0xff]) // JPG图片对应的魔数
        // 执行检查
        if (isPNG(bytes)) {
          const buffer = Buffer.from(reader.result, "base64")
          setImg("data:image/png;base64," + buffer.toString("base64"))
          setName(file.name)
          setLoaidng(true)
          // 格式校验通过
          resolve(file)
          return
        } else if (isJPG(bytes)) {
          const buffer = Buffer.from(reader.result, "base64")
          setImg("data:image/jpeg;base64," + buffer.toString("base64"))
          setName(file.name)
          setLoaidng(true)
          // 格式校验通过
          resolve(file)
          return
        }
        // 校验未通过
        message.error("图片格式校验未通过")
        reject("图片格式校验未通过")
        return
      }
      // 监听文件读取异常
      reader.onerror = reject
      // 开始读取文件
      reader.readAsArrayBuffer(file)
    })
  }

  const uploadprops = {
    accept: ".jpg,.png",
    beforeUpload: async (file, fileList) => {
      return checkImageFile(file)
    },
    showUploadList: false,
  }

  useEffect(() => {
    if (!isUploading) return
    setLoaidng(true)
    axios
      .post(`${BASEURL}/api/sku/icon`, {
        sku_code,
        filename: name,
        filedata: img,
      })
      .then((res) => {
        console.log(res)
        setLoaidng(false)
        if (res && res.status == 200 && res.data.code == 200) {
          message.success(isChinese ? "icon更新成功" : "Updated Ok")
        } else {
          message.error(isChinese ? "icon更新失败" : "Updated Fail")
          // if (res && res.data.data.msg) {
          //   message.error(res.data.data.msg)
          // } else {
          //   message.error(isChinese ? "icon更新失败" : "Updated Fail")
          // }
        }
      })
      .catch((error) => {
        setImg(filename || default_icon)
        setLoaidng(false)
        console.log(error)
        message.error(`${error}`)
      })
  }, [isUploading])

  const handleLock = () => {
    setLocking(true)
    axios
      .get(`/api/sku/lock?sku_code=${sku_code}&is_lock=${lock === 1 ? 0 : 1}`)
      .then((res) => {
        setLocking(false)
        if (res && res.status === 200) {
          message.success(
            `${sku_code}${
              lock === 1
                ? isChinese
                  ? "解锁"
                  : "Unlocked"
                : isChinese
                ? "锁定"
                : "Locked"
            } Successful`
          )
          setLock(lock === 1 ? 0 : 1)
        } else {
          if (res && res.message) {
            message.error(res.message)
          } else {
            message.error(isChinese ? "操作失败" : "Operation Failed")
          }
        }
      })
      .catch((error) => {
        setLocking(false)
        console.log(error)
        message.error(`${error}`)
      })
  }

  let loadingText = ""
  if (isChinese) loadingText = !isUploading ? "更新" : "上传中"
  else loadingText = !isUploading ? "update" : "uploading"
  return (
    <div className="container">
      <Image width={200} height={200} src={img} />
      <div className="content">
        <div className="text">
          <Tooltip title={`[${sku_code}]${sku_name}`}>
            <a style={{ color: "#FF4000" }}>{sku_code}</a>
            {sku_name}
          </Tooltip>
        </div>
        {/* <Button icon={lock ? <LockOutlined /> : <UnlockOutlined />} shape="circle" type={lock ? "danger" : "ghost"} loading={isLocking} onClick={handleLock} /> */}
        <ImgCrop
          rotate
          modalTitle={isChinese ? "图片裁剪" : "image cropper"}
          modalOk={isChinese ? "确认" : "confirm"}
          modalCancel={isChinese ? "取消" : "cancel"}
        >
          <Upload {...uploadprops}>
            <Button
              style={{ height: "30px" }}
              disabled={isUploading}
              type="primary"
              size="small"
              loading={isUploading}
            >
              {loadingText}
            </Button>
          </Upload>
        </ImgCrop>
      </div>
    </div>
  )
}
